#portafolio-page {
    height: 100vh;
    width: 100vw;
    min-height: 480px;
    min-width: 320px;
    overflow: scroll;
    position: relative;
}

#portafolio-page .frame {
    height: 100vh;
    width: 100vw;
    min-height: 480px;
    min-width: 320px;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width: 768px) {
    #portafolio-page {
        min-height: 609px;
    }

    #portafolio-page .frame {
        min-height: 609px;
    }
}

#portafolio-page .top {
    position: absolute;
    /* MARGEN USADO 320x480: 12px */
    top: 12px;
    left: 12px;
}

#portafolio-page .bottom {
    position: absolute;
    bottom: 0;
    width: 100%;
}

#portafolio-page .bottom .title-container {
    display: none;
}

#portafolio-page .top .volver-button {
    display: flex;
    align-items: center;
    width: fit-content;
    text-decoration: none;
}

#portafolio-page .top .volver-button svg {
    width: 28px;
    height: 28px;
    transition: all ease .5s;
}

#portafolio-page .top .volver-button span {
    padding-left: 10px;
    font-weight: 300;
    font-size: 1rem;
    transition: all ease .5s;
}

#portafolio-page .top .volver-button:hover svg {
    fill: #000;
}

#portafolio-page .top .volver-button:hover span {
    color: #000!important;
}

#portafolio-page .bottom .change-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

#portafolio-page .bottom .change-buttons button {
    background-color: transparent;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    /* MARGEN USADO 320x480: 12px */
    padding: 12px;
}

#portafolio-page .bottom .change-buttons button span {
    padding: 0 10px;
    font-size: 1rem;
    font-weight: 300;
    transition: all ease .5s;
}

#portafolio-page .bottom .change-buttons button svg {
    width: 22px;
    height: 22px;
    transition: all ease .5s;
}

#portafolio-page .bottom .change-buttons button:hover svg {
    fill: #000;
}

#portafolio-page .bottom .change-buttons button:hover span {
    color: #000!important;
    cursor: pointer;
}

#portafolio-page .bottom .change-buttons button.left {
    justify-content: flex-start;
}

#portafolio-page .bottom .change-buttons button.right {
    justify-content: flex-end;
}

@media (min-width: 768px) {
    #portafolio-page .top {
        /* MARGEN USADO 768: 30px */
        top: 30px;
        left: 30px;
    }

    #portafolio-page .top .volver-button span {
        font-size: 1.2rem;
    }

    #portafolio-page .top .volver-button svg {
        width: 32px;
        height: 32px;
    }

    #portafolio-page .bottom .change-buttons button {
        /* MARGEN USADO 768: 30px */
        padding: 30px;
    }

    #portafolio-page .bottom .change-buttons button span {
        font-size: 1.2rem;
    }

    #portafolio-page .bottom .change-buttons button svg {
        width: 26px;
        height: 26px;
    }
}

@media (min-width: 1024px) {
    #portafolio-page .top {
        /* MARGEN USADO 1024: 36px */
        top: 36px;
        left: 36px;
    }

    #portafolio-page .bottom {
        display: grid;
        grid-template-columns: 1fr 1fr;
        bottom: 20px;
    }

    #portafolio-page .bottom .title-container {
        display: block;
        /* MARGEN USADO 1024: 36px */
        padding-left: 36px;
    }

    #portafolio-page .title-container .title {
        font-size: 4rem;
        text-transform: uppercase;
        transition: all ease .5s;
    }

    #portafolio-page .bottom .change-buttons button {
        /* MARGEN USADO 1024: 36px */
        padding: 36px;
    }
}

