#header {
    min-height: 320px;
    height: 100vh;
    background-color: #30336B;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

#header::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../../../assets/header-drawing.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .1;
}

#header .text {
    position: relative;
    text-align: center;
    color: #fff;
}

#header .text h2 {
    font-weight: 200;
    font-size: 1.5rem;
    color: #DDDDDD;
}

#header .text h1 {
    font-weight: 500;
    font-size: 2rem;
}

@media (min-width: 768px) {
    #header .text h2 {
        font-size: 2rem;
    }

    #header .text h1 {
        font-size: 2.5rem;
    }
}

@media (min-width: 1366px) {
    #header .text h2 {
        font-size: 2.5rem;
    }

    #header .text h1 {
        font-size: 3rem;
    }
}