#contacto {
    height: 100vh;
    /* min-height: 611px; */
    min-height: 576px;
    background-color: #5b2462;
    position: relative;
    z-index: 1;
}

#contacto::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../../../assets/contact-pattern.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.15;
    z-index: 2;
    top: 0;
    left: 0;
}

#contacto .section-title {
    color: #fff;
    padding: 20px 0 0 20px;
}

#contacto .wrapper {
    position: relative;
    z-index: 3;
}

#contacto .wrapper .info {
    display: none;
}

@media (min-width: 1024px) {
    #contacto {
        background-color: transparent;
    }

    #contacto::before {
        background-image: none;
    }

    #contacto .section-title {
        padding: 0;
    }

    #contacto .wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 100%;
    }

    #contacto .wrapper .info {
        display: block;
        background-color: #5b2462;
        height: 100%;
        padding: 35px;
        position: relative;
    }

    #contacto .wrapper .info::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-image: url(../../../assets/contact-pattern.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        opacity: 0.15;
        z-index: 2;
        top: 0;
        left: 0;
    }

    #contacto .wrapper .info .text {
        position: relative;
        z-index: 3;
    }

    #contacto .wrapper .info p {
        color: rgba(255, 255, 255, 0.5);
        font-weight: 200;
        font-size: 1.5rem;
        padding-top: 30px;
    }

    #contacto .wrapper .info ul {
        list-style: none;
        padding-top: 30px;
        /* position: absolute;
        bottom: 35px;
        left: 35px; */
    }

    #contacto .wrapper .info ul li {
        display: flex;
        align-items: center;
        padding: 10px 0;
    }

    #contacto .wrapper .info ul li svg {
        fill: #fff;
        width: 33px;
        height: 33px;
    }

    #contacto .wrapper .info ul li span {
        color: rgba(255, 255, 255, 0.5);
        font-size: 1.5rem;
        font-weight: 200;
        padding-left: 15px;
    }
}

@media (min-width: 1366px) {
    #contacto .wrapper .info {
        padding: 50px;
    }

    #contacto .wrapper .info p {
        font-size: 1.8rem;
    }

    #contacto .wrapper .info ul li svg {
        width: 38px;
        height: 38px;
    }

    #contacto .wrapper .info ul li span {
        font-size: 1.8rem;
        padding-left: 20px;
    }
}

#contacto .wrapper .contact-form {
    margin: 0 20px;
}

@media (min-width: 768px) {
    #contacto .wrapper .contact-form .form-row {
        width: 60%;
    }
}

@media (min-width: 1024px) {
    #contacto .wrapper .contact-form .form-row {
        width: 100%;
    }
}

#contacto .wrapper .contact-form .form-row .input-container {
    padding: 10px 0;
}

#contacto .wrapper .contact-form .form-row .input-container label {
    color: #fff;
    font-weight: 300;
    padding-bottom: 5px;
    display: block;
}

#contacto .wrapper .contact-form .form-row .input-container input,
#contacto .wrapper .contact-form .form-row .input-container textarea {
    background-color: rgba(255, 255, 255, 0.1);
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 10px 10px;
    color: #fff;
    font-weight: 300;
    transition: all ease 0.3s;
    display: block;
    width: 100%;
    border: solid 1px rgba(255, 255, 255, 0.3);
}

#contacto .wrapper .contact-form .form-row .input-container textarea {
    resize: none;
    height: 100px;
}

#contacto .wrapper .contact-form .form-row .input-container input:hover,
#contacto .wrapper .contact-form .form-row .input-container textarea:hover {
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
}

#contacto .wrapper .contact-form .form-row .input-container input:focus,
#contacto .wrapper .contact-form .form-row .input-container textarea:focus {
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    background-color: #642f6a;
}

#contacto .wrapper .contact-form .submit-button button {
    background-color: #fff;
    color: #5b2462;
    margin: 20px 0;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    width: 100%;
    transition: all ease 0.3s;
    border: solid 1px #fff;
}

#contacto .wrapper .contact-form .submit-button button:hover {
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    background-color: transparent;
    color: #fff;
}

@media (min-width: 768px) {
    #contacto .wrapper .contact-form .submit-button {
        display: flex;
        justify-content: flex-end;
    }

    #contacto .wrapper .contact-form .submit-button button {
        width: 30%;
    }
}

@media (min-width: 1024px) {
    #contacto .wrapper .contact-form {
        padding: 20px;
    }

    #contacto .wrapper .contact-form .form-row.double {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
    }

    #contacto .wrapper .contact-form .form-row .input-container label {
        color: #5b2462;
        font-size: 1.1rem;
        padding-bottom: 8px;
    }

    #contacto .wrapper .contact-form .form-row .input-container input,
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        background-color: rgba(0, 0, 0, 0.02);
        border: solid 1px rgba(0, 0, 0, 0.1);
        color: #000;
    }

    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 200px;
    }

    #contacto .wrapper .contact-form .form-row .input-container input:focus,
    #contacto .wrapper .contact-form .form-row .input-container textarea:focus {
        background-color: #fff;
    }

    #contacto .wrapper .contact-form .submit-button button {
        background-color: #5b2462;
        border-color: #5b2462;
        color: #fff;
        font-size: 1.1rem;
    }

    #contacto .wrapper .contact-form .submit-button button:hover {
        color: #5b2462;
    }
}

@media (min-width: 1024px) and (min-height: 611px) {
    #contacto .wrapper .contact-form .form-row .input-container input,
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        font-size: 1.1rem;
    }

    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 230px;
    }
}

@media (min-width: 1024px) and (min-height: 700px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 320px;
    }
}

@media (min-width: 1024px) and (min-height: 750px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 380px;
    }
}

@media (min-width: 1366px) {
    #contacto .wrapper .contact-form {
        padding: 30px;
    }

    #contacto .wrapper .contact-form .form-row .input-container label {
        font-size: 1.3rem;
    }

    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 160px;
    }

    #contacto .wrapper .contact-form .submit-button button {
        font-size: 1.3rem;
    }
}

@media (min-width: 1366px) and (min-height: 600px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 180px;
    }
}

@media (min-width: 1366px) and (min-height: 698px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 280px;
    }
}

@media (min-width: 1366px) and (min-height: 768px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 350px;
    }
}

@media (min-width: 1366px) and (min-height: 850px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 430px;
    }
}

@media (min-width: 1366px) and (min-height: 900px) {
    #contacto .wrapper .contact-form .form-row .input-container textarea {
        height: 470px;
    }
}