.carousel {
    overflow: hidden;
}

.carousel .carousel-wrapper {
    width: fit-content;
    display: flex;
}

@media (min-width: 1024px) {
    .carousel .carousel-wrapper {
        height: 100%;
        display: flex;
        align-items: center;
    }
}

.carousel .carousel-wrapper .image {
    width: 100vw;
    display: flex;
    justify-content: center;
    padding: 20px 0;
}

@media (min-width: 1024px) {
    .carousel .carousel-wrapper .image {
        width: 50vw;
        height: fit-content;
    }
}

@media (min-width: 1366px) {
    .carousel .carousel-wrapper .image {
        width: 65vw;
    }
}

.carousel .carousel-wrapper .image img {
    /* height: 400px; */
    width: 200px;
    /* height: 100%; */
    border-radius: 10px;
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

@media (min-width: 1366px) {
    .carousel .carousel-wrapper .image img {
        width: 735px;
    }
}

@media (min-width: 1366px) and (min-height: 725px) {
    .carousel .carousel-wrapper .image img {
        width: 800px;
    }
}

@media (min-width: 1024px) and (max-width: 1365px) {
    .carousel .carousel-wrapper .image img {
        width: 250px;
    }
}

@media (min-width: 1024px) and (min-height: 698px) and (max-width: 1365px) {
    .carousel .carousel-wrapper .image img {
        width: 270px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .carousel .carousel-wrapper .image img {
        width: 650px;
    }
}

@media (min-height: 700px) and (min-width: 768px) and (max-width: 1023px) {
    .carousel .carousel-wrapper .image img {
        width: 720px;
    }
}

@media (min-height: 650px) and (max-width: 767px) and (max-width: 1023px) {
    .carousel .carousel-wrapper .image img {
        width: 210px;
    }
}

@media (min-height: 700px) and (max-width: 767px) and (max-width: 1023px) {
    .carousel .carousel-wrapper .image img {
        width: 240px;
    }
}

@media (min-height: 750px) and (max-width: 767px) {
    .carousel .carousel-wrapper .image img {
        width: 260px;
    }
}
