@media (min-width: 768px) {
    .slider {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

@media (min-width: 1024px) {
    .slider {
        display: grid;
        grid-template-columns: 3fr 2fr;
    }
}

.slider .images {
    /* DIMENSIONES IMAGEN 320x480: 180x340 */
    width: 180px;
    height: 340px;
    display: flex;
    border-radius: 5px;
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    overflow: hidden;
}

.slider .leer-mas {
    display: flex;
    justify-content: center;
    padding-top: 5px;
}

@media (min-width: 768px) {
    .slider .images-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .slider .images {
        /* DIMENSIONES IMAGEN 768: 230x440 */
        width: 230px;
        height: 440px;
    }

    .slider .leer-mas {
        display: none;
    }
}

@media (min-width: 1024px) {
    .slider {
        /* CORRECCIÓN DE CENTRADO */
        margin-top: -50px;
    }

    .slider .images {
        /* DIMENSIONES IMAGEN 1024: 550x310 */
        width: 550px;
        height: 310px;
    }
}

@media (min-width: 1366px) {
    .slider .images {
        width: 650px;
        height: 365px;
    }
}

.slider .leer-mas button {
    background-color: #000;
    color: #fff;
    border: none;
    outline: none;
    font-size: 1rem;
    padding: 5px 10px;
    font-weight: 300;
    border-radius: 5px;
    /* ANCHO IMAGEN (Usado en boton) 320x480: 180 */
    width: 180px;
    transition: all ease 0.5s;
}

.slider .leer-mas button:hover {
    background-color: #000 !important;
    cursor: pointer;
}

.slider .images img {
    width: 100%;
    object-fit: cover;
}

.slider .text-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .6);
    z-index: 10;
    min-height: 480px;
}

.slider .text-container button {
    position: absolute;
    background-color: transparent;
    border: none;
    outline: none;
    top: 10px;
    right: 10px;
    padding: 5px;
}

.slider .text-container button svg {
    fill: #fff;
    width: 36px;
    height: 36px;
    transition: all ease .5s;
}

.slider .text-container button:hover svg {
    fill: #000;
}

.slider .text-container .text {
    background-color: #fff;
    width: 90%;
    padding: 20px;
    border-radius: 5px;
}

.slider .text-container .text .technologies img {
    height: 38px;
    padding-right: 20px;
}

.slider .text-container .text h2 {
    font-weight: 500;
}

.slider .text-container .text p {
    font-weight: 300;
    padding: 15px 0;
}

.slider .text-container .text h3 {
    font-weight: 400;
}

.slider .text-container .text .technologies {
    padding: 10px 0;
}

@media (min-width: 768px) {
    .slider .text-container {
        position: static;
        background-color: transparent;
        min-height: auto;
    }

    .slider .text-container .text {
        width: 90%;
        padding: 0;
    }

    .slider .text-container button {
        display: none;
    }

    .slider .text-container .text h2 {
        font-size: 1.8rem;
    }

    .slider .text-container .text p {
        font-size: 1.2rem;
    }

    .slider .text-container .text h3 {
        font-size: 1.4rem;
    }
}

@media (min-width: 1024px) {
    .slider .text-container .text {
        width: 80%;
    }
}

