.error-container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 20;
    top: 0;
    left: 0;
    pointer-events: none;
}

.error-container .error {
    width: 100vw;
    height: 100vh;
    position: relative;
}

.error-container .error p {
    background-color: red;
    padding: 15px;
    color: #fff;
    font-weight: 300;
    width: 250px;
    -webkit-box-shadow: -4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: -4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    box-shadow: -4px 4px 10px 0px rgba(0, 0, 0, 0.3);
    position: absolute;
    bottom: 20px;
    right: 0;
    pointer-events: all;
}

.error-container .error.success p {
    background-color: green;
}
