#portafolio {
    height: 100vh;
    /* min-height: 576px; */
    min-height: 611px;
    max-height: 768px;
}

@media (min-width: 1024px) {
    #portafolio .wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 100%;
    }
}

@media (min-width: 1366px) {
    #portafolio {
        max-height: 900px;
    }

    #portafolio .wrapper {
        grid-template-columns: 35fr 65fr;
    }
}

@media (min-width: 1440px) {
    #portafolio {
        max-height: 1000;
    }
}

#portafolio .wrapper .text {
    padding: 20px;
    color: #30336b;
}

#portafolio .wrapper .text p {
    font-weight: 300;
    padding: 10px 0;
}

#portafolio .wrapper .text a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #30336b;
    font-weight: 300;
    border-bottom: 1px solid #30336b;
    width: fit-content;
}

@media (min-width: 768px) {
    #portafolio .wrapper .text p,
    #portafolio .wrapper .text a {
        font-size: 1.2rem;
    }
}

@media (min-width: 1024px) {
    #portafolio .wrapper .text {
        height: 100%;
        display: flex;
        align-items: center;
        padding: 40px;
    }

    #portafolio .wrapper .text .text-wrapper {
        margin-top: -65px;
    }

    #portafolio .wrapper .text p,
    #portafolio .wrapper .text a {
        font-size: 1.5rem;
    }
}

@media (min-width: 1366px) {
    #portafolio .wrapper .text {
        padding: 60px;
    }

    #portafolio .wrapper .text .section-title {
        font-size: 4rem;
    }

    #portafolio .wrapper .text p {
        padding: 30px 0;
        font-size: 1.8rem;
    }

    #portafolio .wrapper .text a {
        font-size: 1.8rem;
    }
}

#portafolio .wrapper .text a:hover {
    color: #000;
    cursor: pointer;
    border-width: 2px;
    border-color: #000;
}

#portafolio .wrapper .text a:hover svg {
    fill: #000;
}

#portafolio .wrapper .text a svg {
    fill: #30336b;
    padding-left: 10px;
    width: 35px;
    height: 35px;
}
